"use client";

import { useState, useEffect, ChangeEvent, FormEvent } from "react";
import { useRouter, useParams } from "next/navigation";

type BoardImage = { name: string; src: string }; // ÁîªÂÉèÂêç„Å®URL

type PracticeRecord = {
  lessonId: string;
  practiceDate: string;
  reflection: string;
  boardImages: BoardImage[];
};

export default function PracticeAddPage() {
  const router = useRouter();
  const { id } = useParams() as { id: string };

  const [practiceDate, setPracticeDate] = useState("");
  const [reflection, setReflection] = useState("");
  const [boardImages, setBoardImages] = useState<BoardImage[]>([]);
  const [uploading, setUploading] = useState(false);

  // ÈÅéÂéªË®òÈå≤„Åå„ÅÇ„Çå„Å∞„É≠„Éº„Éâ
  useEffect(() => {
    const recordsJson = localStorage.getItem("practiceRecords") || "[]";
    try {
      const records: PracticeRecord[] = JSON.parse(recordsJson);
      const rec = records.find((r) => r.lessonId === id);
      if (rec) {
        setPracticeDate(rec.practiceDate);
        setReflection(rec.reflection);
        setBoardImages(rec.boardImages);
      }
    } catch {
      // ignore
    }
  }, [id]);

  // ÁîªÂÉèÈÅ∏Êäû ‚Üí „É≠„Éº„Ç´„É´URL„Åß„Éó„É¨„Éì„É•„ÉºÁî®„Å´„Çª„ÉÉ„Éà
  const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files) return;
    const files = Array.from(e.target.files);
    const newImages = files.map((file) => ({
      name: file.name,
      src: URL.createObjectURL(file),
    }));
    setBoardImages((prev) => [...prev, ...newImages]);
    e.target.value = "";
  };

  // ÁîªÂÉèÂâäÈô§
  const handleRemoveImage = (index: number) => {
    setBoardImages((prev) => prev.filter((_, i) => i !== index));
  };

  // „É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Å´‰øùÂ≠ò
  const saveToLocal = () => {
    const recordsJson = localStorage.getItem("practiceRecords") || "[]";
    let records: PracticeRecord[] = [];
    try {
      records = JSON.parse(recordsJson);
    } catch {
      // ignore
    }
    const record: PracticeRecord = { lessonId: id, practiceDate, reflection, boardImages };
    const idx = records.findIndex((r) => r.lessonId === id);
    if (idx >= 0) records[idx] = record;
    else records.push(record);
    localStorage.setItem("practiceRecords", JSON.stringify(records));
  };

  // Google Drive„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÔºàPDF blob„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÔºâ
  const uploadPdfToDrive = async (blob: Blob) => {
    const accessToken = process.env.NEXT_PUBLIC_GOOGLE_DRIVE_ACCESS_TOKEN;
    if (!accessToken) throw new Error("Google Drive„Ç¢„ÇØ„Çª„Çπ„Éà„Éº„ÇØ„É≥„ÅåË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì");

    const metadata = {
      name: `practice_record_${id}.pdf`,
      mimeType: "application/pdf",
    };

    const formData = new FormData();
    formData.append("metadata", new Blob([JSON.stringify(metadata)], { type: "application/json" }));
    formData.append("file", blob);

    const res = await fetch(
      "https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id,webViewLink",
      {
        method: "POST",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        body: formData,
      }
    );

    if (!res.ok) {
      const text = await res.text();
      throw new Error(`Google Drive„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÂ§±Êïó: ${text}`);
    }

    return res.json();
  };

  // ‰øùÂ≠ò„Éú„Çø„É≥Êäº‰∏ãÂá¶ÁêÜ
  const handleSave = async (e: FormEvent) => {
    e.preventDefault();

    if (!practiceDate.trim()) {
      alert("ÂÆüÊñΩÊó•„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ");
      return;
    }
    if (!reflection.trim()) {
      alert("ÊåØ„ÇäËøî„Çä„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ");
      return;
    }

    saveToLocal();

    try {
      const { default: html2pdf } = await import("html2pdf.js");
      const el = document.getElementById("practice-preview");
      if (!el) {
        alert("„Éó„É¨„Éì„É•„Éº„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì");
        return;
      }
      const pdfBlob = await html2pdf().from(el).outputPdf("blob");
      await uploadPdfToDrive(pdfBlob);
      alert("„É≠„Éº„Ç´„É´‰øùÂ≠ò„Å®Google Drive‰øùÂ≠ò„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„ÅüÔºÅ");
      router.push("/practice/history");
    } catch (error) {
      alert("Google Drive„Å∏„ÅÆ‰øùÂ≠ò„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ");
      console.error(error);
    }
  };

  return (
    <main style={{ padding: 24, maxWidth: 800, margin: "auto", fontFamily: "sans-serif" }}>
      <h2>ÂÆüË∑µË®òÈå≤‰ΩúÊàê„ÉªÁ∑®ÈõÜ</h2>

      <form onSubmit={handleSave}>
        <label style={{ display: "block", marginBottom: 12 }}>
          ÂÆüÊñΩÊó•Ôºö
          <input
            type="date"
            value={practiceDate}
            onChange={(e) => setPracticeDate(e.target.value)}
            required
            style={{ width: "100%", padding: 8, marginTop: 4 }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          ÊåØ„ÇäËøî„ÇäÔºö
          <textarea
            value={reflection}
            onChange={(e) => setReflection(e.target.value)}
            required
            rows={6}
            style={{ width: "100%", padding: 8, marginTop: 4 }}
          />
        </label>

        <label
          style={{
            display: "block",
            marginBottom: 12,
            padding: 8,
            backgroundColor: "#1976d2",
            color: "#fff",
            borderRadius: 6,
            textAlign: "center",
            cursor: "pointer",
          }}
        >
          üì∑ ÊùøÊõ∏ÂÜôÁúü„Çí„Ç¢„ÉÉ„Éó„É≠„Éº„Éâ
          <input
            type="file"
            multiple
            accept="image/*"
            onChange={handleFileChange}
            style={{ display: "none" }}
          />
        </label>

        <div style={{ marginBottom: 16 }}>
          {boardImages.map((img, i) => (
            <div
              key={`${img.name}-${i}`}
              style={{ position: "relative", marginBottom: 12 }}
            >
              <img
                src={img.src}
                alt={img.name}
                style={{ width: "100%", borderRadius: 8, border: "1px solid #ccc" }}
              />
              <button
                type="button"
                aria-label="ÁîªÂÉè„ÇíÂâäÈô§"
                onClick={() => handleRemoveImage(i)}
                style={{
                  position: "absolute",
                  top: 8,
                  right: 8,
                  backgroundColor: "rgba(229, 57, 53, 0.85)",
                  border: "none",
                  borderRadius: "50%",
                  color: "white",
                  width: 28,
                  height: 28,
                  cursor: "pointer",
                  fontWeight: "bold",
                }}
              >
                √ó
              </button>
            </div>
          ))}
        </div>

        {/* „Éó„É¨„Éì„É•„Éº */}
        <section
          id="practice-preview"
          style={{
            padding: 16,
            border: "1px solid #ccc",
            borderRadius: 6,
            backgroundColor: "#fff",
            fontSize: 14,
            lineHeight: 1.6,
            marginBottom: 16,
          }}
        >
          <h3>ÂÆüÊñΩÊó•: {practiceDate}</h3>
          <h3>ÊåØ„ÇäËøî„Çä</h3>
          <p>{reflection}</p>

          {boardImages.length > 0 && (
            <div style={{ marginTop: 12 }}>
              <strong>ÊùøÊõ∏ÂÜôÁúü</strong>
              <div style={{ marginTop: 8, display: "flex", flexDirection: "column", gap: 12 }}>
                {boardImages.map((img, i) => (
                  <img
                    key={`${img.name}-${i}-preview`}
                    src={img.src}
                    alt={img.name}
                    style={{ width: "100%", borderRadius: 8, border: "1px solid #ccc" }}
                  />
                ))}
              </div>
            </div>
          )}
        </section>

        <button
          type="submit"
          style={{
            padding: 12,
            backgroundColor: "#4CAF50",
            color: "white",
            border: "none",
            borderRadius: 6,
            width: "100%",
            cursor: "pointer",
          }}
        >
          üíæ ‰øùÂ≠òÔºà„É≠„Éº„Ç´„É´ÔºãDriveÔºâ
        </button>
      </form>
    </main>
  );
}
